import { injectScript } from './utils/inject'
import moment from 'moment'

export class Market {

  intervel

  codes = {
    // 指数代码: []
  }

  weights = {
    // 指数代码: [{ code: 代码, name: 简称, weight: 权重 }]
  }

  stocks = {
    // code : { Stock }
  }

  // 深交所上市股票代码: 000 (主板)、002（中小板）、300 (创业板)、12 (可转债)
  // 上交所上市股票代码: 60 (主板)、688（科创板）、11 (可转债)
  market(code) {
    const section = code.slice(0, 2)

    if (['00', '30', '12'].includes(section)) {
      return 'sz'
    }

    if (['60', '68', '11'].includes(section)) {
      return 'sh'
    }
  }

  // 仅限上证50、中证500、沪深300
  async captureStockWeight(code) {
    try {
      // 以权重倒序组织
      // eg. "601318,中国平安,金融,深圳市,5.83,38.82,14.9,182.8024141,182.8024141,92.19,9986.6334007062,12.27,14.241214,000016,2020-12-04,92.19,0.35,489295,4464927488"
      const url = `http://datainterface.eastmoney.com/EM_DataCenter/JS.aspx?type=SHSZZS&sty=SHSZZS&st=2&sr=-1&p=1&ps=500&js=var%20bSLQbtLZ={pages:(pc),data:[(x)]}&code=${code}`

      await injectScript(url)

      this.weights[code] = {}
      this.codes[code] = []

      for (let section of window.bSLQbtLZ.data) {
        const s = section.split(',')

        const mcode = this.market(s[0]) + s[0]
        const stock = { code: mcode, name: s[1], weight: +s[12] }

        this.weights[code][mcode] = stock
        this.codes[code].push(mcode)
      }

      return this.weights[code]
    } catch {
      // ...
    }
  }

  async captureStocks(codes) {
    try {
      const url = `http://hq.sinajs.cn/list=${codes.join(',')}`

      await injectScript(url)

      const stocks = []

      for(let code of codes) {
        try {
          // eg. var hq_str_sz002197="证通电子,8.840,8.840,8.860,8.890,8.800,8.860,8.870,4278583,37870587.550,176800,8.860,83217,8.850,37600,8.840,92300,8.830,56300,8.820,43800,8.870,143900,8.880,118500,8.890,85400,8.900,21100,8.910,2020-12-04,15:00:03,00";
          const section = window[`hq_str_${code}`].split(',')

          const name = section[0]
          const close = +section[2]
          const price = +section[3]
          const change =  (price === 0) ? 0 : +((price - close) / close * 100).toFixed(2)

          const stock = { code, name, close, price, change }
          
          this.stocks[code] = stock

          stocks.push(stock)
        } catch {
          // ...
        }
      }

      return stocks
    } catch {
      // ...
    }
  }

  async start(code, callback = () => {}) {
    if (!this.codes[code]) {
      await this.captureStockWeight(code)
    }

    const hook = async () => {
      const stocks = await this.captureStocks(this.codes[code])

      // 增加权重信息
      for(let stock of stocks) {
        stock.weight = this.weights[code][stock.code].weight
      }

      callback(stocks)
    }

    await hook()

    // 周末
    if (moment().weekday() === 6 || moment().weekday() === 0) { return }

    this.intervel = setInterval(async () => {
      const time = +moment().format('HHmmss')
      // 交易时间
      if ((time > 93000 && time < 113000) || (time > 130000 && time < 150000)) {
        hook()
      }
    }, 5*1000)
  }

  async stop() {
    clearInterval(this.intervel)
  }
}


export default new Market()