<template>
  <div class="matrix">
    <ul class="nav">
      <li @click="select('000016')" :class="{ selected: selected === '000016' }">上证50</li> 
      <li @click="select('000300')" :class="{ selected: selected === '000300' }">沪深300</li> 
      <li @click="select('000905')" :class="{ selected: selected === '000905' }">中证500</li> 
    </ul>
    <ui-chart ref="chart"></ui-chart>
    <p class="tips">数据实时更新，纵轴为涨幅, 横轴为权重。(红涨绿跌, 圆圈越大权重越大)</p>
  </div>
</template>

<script>
import notification from '../notification'
import market from '../market'

window.__walle_notifition = notification
window.__walle_market = market

export default {
  data() {
    return {
      selected: '000016',
    }
  },
  mounted() {
    // notification.start()
    this.select('000016')
  },
  destroyed() {
    notification.stop()
    market.stop()
  },
  methods: {
    select(code) {
      this.selected = code

      market.stop()
      market.start(code, (stocks) => {
        console.log('.')
        this.$refs.chart.update({ stocks })
      })
    }
  }
}
</script>

