import toastr from './utils/toastr'

export class Notification {

  messages = []
  timestamp = null
  interval = -1

  ajax(type = 'GET', url, data, opts = {} ) {
    return new Promise((r, j) => {
      // console.log(`#ajax# request - ${type} - ${url} `, data)

      window.$.ajax(Object.assign({
        type,
        url,
        data,
        async: opts.async || true,
        dataType: opts.dataType || 'html',
        // cors with cookies 
        // crossDomain: true,
        // xhrFields: {withCredentials: true},

        success: (...args) => {
          // console.log('#ajax# success - ', ...args)
          r(...args)
        },
        error:(...args) => {
          // console.log('#ajax# fail - ', ...args)
          j(...args)
        },
      }, opts));
    })
  }

  async pull() {
    try {
      const messages = await this.ajax('GET', 'http://m.fbecn.com/24h/news_fbe5.json?newsid=0', null, { dataType: 'json' })

      this.messages = messages.list || []

      return this.messages
    } catch {
      // ... 
    }
  }

  async push() {
    try {
      const message = this.messages[0]

      // 避免重复推送
      if (message && this.timestamp !== message.time) {
        this.timestamp = message.time

        const options = { type: 'info', timeOut: 30*1000, extendedTimeOut: 30*1000 } 
        toastr.confirm(message.content, '',() => {}, options)
      }

    } catch {
      // ...
    }
  }

  async start() {
    try {
      this.interval = setInterval(async () => {
        await this.pull()
        await this.push()
      }, 5*60*1000)
    } catch {
      // ...
    }
  }

  async stop() {
    clearInterval(this.interval)
  }

} 

export default new Notification()