
import '../libs/toastr.css'
import toastr from '../libs/toastr'

// register as global
window.toastr = toastr

const defaultOptions = { timeOut: 0, extendedTimeOut: 0, closeButton: true, newestOnTop: false, positionClass: "toast-bottom-right" }

toastr.confirm = (msg, title, cb, options = {}) => {
  // merge default options
  options = Object.assign({}, defaultOptions, options)

  return new Promise(resolve => {
    options.onclick = () => { 
      resolve()
      cb && cb()
    }
    options.onCloseClick = () => {
      // 清空所有模态框 
      toastr.clear()
    }

    const type = options.type || 'info'
    toastr[type](msg, title, options)
  })
}

// loading 隐藏通过 toastr.clear(el)方式
toastr.loading = (msg, title) => {
  // merge default options
  const options = Object.assign({}, defaultOptions, { positionClass: "toast-bottom-center" })
  const el = toastr.info(msg || '', title || 'loading ...', options)

  return new Promise(r => setTimeout(r(el), 100))
}

toastr.clearAll = (type) => {
  if (type) {
    // 仅清空同类型的模态框 
    toastr.getContainer().children(`.toast-${type}`).remove() 
  } else {
    // 清空所有模态框 
    toastr.getContainer().remove()
  }
}

export default toastr